body{
  background-color: #212121;
  font-family: 'Courier New', Courier, monospace;
}

.nav-link:hover{
  @apply text-[#30CBEF] transition ease-in duration-200
}

.code-font {
  font-family: "VT323", monospace;
  font-weight: 400;
  font-style: normal;
}

.section-heading{
  @apply code-font lowercase text-6xl text-white text-center;
}
.section-width {
  @layer w-lg-3/4
}

.body-color{
  color: #E6DB74;
}

.button-style{
  @apply code-font rounded bg-gray-400 w-fit p-4
}
.button-style:hover{
  @apply bg-gray-800 w-fit p-4 body-color transition ease-in duration-200
}